* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}

.overall {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: relative;
    height: 100vh;
}

.form {
    width: 400px;
    max-width: 90%;
    border: 2px solid black;
    border-radius: 10px;
    padding: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

button {
    cursor: pointer;
    border: 1px solid black;
    background-color: #fff;
    text-align: center;
    color: #000;
    width: 50px;
    font-size: 1.3rem;
    text-align: center;
    border-radius: 5px;
    display: grid;
    place-items: center;
}

button:hover {
    background-color: #000;
    color: #fff;
    transition: ease-in 0.5s;
}

ul {

    list-style: none;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 50px;
    width: 100%;
}

ul li {
    border: 1px solid black;
    border-radius: 5px;
    color: #000;
    width: 90%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 7px;
    margin-block: 10px;
    font-size: 1.15rem;
}

.todo {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    height: 250px;
    width: 100%;
    text-align: center;
}

.todo>div {
    padding: 20px;
    width: 100%;
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
}

label {
    font-size: 1.15rem;
}

input {
    border: 2px solid;
    width: 100%;
    padding: 0.35rem;
    outline: none;
}

.btn {
    width: 100px;
    font-size: 1.18rem;
    padding: 7px 5px;
    background-color: #000;
    color: #fff;
}

.btn:hover {
    background-color: #fff;
    color: #000;
}

.error {
    display: block;
    color: red;
    font-style: italic;
}
